export default function setBodyColor (router) {
  router.beforeEach((to, from, next) => {
    const bodyColors = {
      '/': { main: '#001e2bff', gradient: '#eeeeee' },
      '/lotto': { main: '#f1775f', gradient: '#eeeeee' },
      '/qrcode': { main: '#45be99', gradient: '#eeeeee' },
      '/emoticon': { main: '#7b7ee7', gradient: '#ffffff' },
      '/canvas': { main: '#b19e40ff', gradient: '#ffffff' },
      '/help': { main: '#52a753ff', gradient: '#ffffff' },
      '/contactus': { main: '#52a753ff', gradient: '#ffffff' }

    }

    const { main: newBodyColor, gradient: secondaryColor } = bodyColors[to.path] || { main: '#eeeeee', gradient: '#ffffff' }

    document.body.style.backgroundImage = `linear-gradient(to bottom, ${newBodyColor} 300px, ${secondaryColor} 300px)`
    next()
  })
}
