import { createRouter, createWebHashHistory } from 'vue-router'
import home from '../views/HomeView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: home,
    meta: { title: 'Home' }
  },
  {
    path: '/contactus',
    name: 'contactus',
    component: () => import('../views/ContactUs.vue'),
    meta: { title: 'contactus' }
  },
  {
    path: '/aboutus',
    name: 'aboutus',
    component: () => import('../views/AboutUs.vue'),
    meta: { title: 'aboutus' }
  },
  {
    path: '/emoticon',
    name: 'emoticon',
    component: () => import('../views/EmoticonMenu.vue'),
    meta: { title: 'EmoticonMenu' }
  },

  {
    path: '/css',
    name: 'css',
    component: () => import('../views/CssMenu.vue'),
    meta: { title: 'CssMenu' }
  },
  {
    path: '/canvas',
    name: 'canvas',
    component: () => import('../views/Canvas.vue'),
    meta: { title: 'ImageTextDown' }
  },

  {
    path: '/qrcode',
    name: 'qrcode',
    component: () => import('../views/QrcodeGenerator.vue'),
    meta: { title: 'QRCode' }
  },
  {
    path: '/lotto',
    name: 'lotto',
    component: () => import('../views/LottoGenerator.vue'),
    meta: { title: 'Lotto' }
  },
  {
    path: '/news',
    name: 'news',
    component: () => import('../views/NewsView.vue'),
    meta: { title: 'news' }
  },
  {
    path: '/blog',
    name: 'blog',
    component: () => import('../views/BlogView.vue'),
    meta: { title: 'blog' }
  },
  {
    path: '/help',
    name: 'help',
    component: () => import('../views/HelpView.vue'),
    meta: { title: 'help' }
  },
  {
    path: '/:pathMatch(.*)*',
    redirect: '/'
  }
]
const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})

export default router
